.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: flex;
    align-items: center;
    justify-content: center;
    /*display: none;*/
}

.popup__content {
    width: 430px;
    min-height: 330px;
    background-color: #fff;
    border-radius: 10px;
    position: relative;
    box-sizing: border-box;
    padding: 34px 36px;
}

@media screen and (max-width: 540px) {
    .popup__content {
        width: calc(100% - 60px);
    }
}

.popup__close {
    width: 26px;
    position: absolute;
    top: -26px;
    right: -26px;
    cursor: pointer;
}

.popup__close-edit {
    width: 26px;
    position: absolute;
    top: -26px;
    right: -26px;
    cursor: pointer;
}
.popup__title {
    color: black;
     margin: 0;
     font-size: 24px;
     line-height: 30px;
 }

.popup__form {
    margin-top: 60px;
}


.popup__input {
    width: 100%;
    height: 47px;
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    font-size: 14px;
    line-height: 17px;
    box-sizing: border-box;
    /* padding: 5px 6px 13px;
    margin-bottom: 24px; */
    padding: 0 10px;
    margin: 10px 0;
}
.button {
    background-color: transparent;
    border: 1px solid white;
    color: white;
    width: 150px;
    height: 50px;
    font-size: 30px;
    display: block;
    cursor: pointer;
}

.button:focus {
    outline-color: greenyellow;
}
.popup__button {
    width: 100%;
    height: 50px;
    border: 1px solid rgba(0, 0, 0, .2);
    margin-top: 48px;
    /* color: rgba(0, 0, 0, .2); */
    color: white;
    font-size: 36px;
    background-color: black;
}
.popup__button:disabled {
    background-color: white;
    color: rgb(159, 159, 159);
}
.popup__button_user {
    margin-top: 48px;
    color: white;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    background-color: black;
}